      .services__item:hover .services__icon path {
        fill: white; }
      .services__item:hover .services__icon circle {
        fill: white; }
      .services__item:hover .services__title::after {
        background-color: white; }
  .services__title {
    position: relative;
    margin-top: 45px;
    margin-bottom: 0;
    font-size: 18px; }
    .services__title::after {
