.portfolio {
  background-color: #F4F4F4;
  padding-bottom: 165px; }
  .portfolio__container {
    display: flex;
    flex-direction: column;
    margin-left: auto;
    margin-right: auto;
    max-width: 1366px;
    padding: 0 20px;
    position: relative; }
  .portfolio__list {
