.slider {
  height: 50vh;
  position: relative;
  overflow: hidden;
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-end;
  justify-content: center; }
  .slider__nav {
    width: 24px;
    height: 24px;
    margin: 2rem 24px;
    border-radius: 50%;
    z-index: 10;
    outline: 12px solid #212227;
    outline-offset: -12px;
    box-shadow: 0 0 0 0 #1769FF, 0 0 0 0 rgba(23, 105, 255, 0);
    cursor: pointer;
    appearance: none;
    backface-visibility: hidden; }
    .slider__nav:checked {
      animation: check 0.3s linear forwards; }
      .slider__nav:checked:nth-of-type(1) ~ .slider__inner {
        transform: translateX(0%); }
      .slider__nav:checked:nth-of-type(2) ~ .slider__inner {
        transform: translateX(-33.33333%); }
      .slider__nav:checked:nth-of-type(3) ~ .slider__inner {
        transform: translateX(-66.66667%); }
  .slider__inner {
    position: absolute;
    top: 0;
    left: 0;
    width: 300%;
    height: 100%;
    transition: all 1s ease-out;
    display: flex;
    flex-flow: row nowrap; }
  .slider__contents {
    height: 100%;
    text-align: center;
    display: flex;
    flex: 1;
    flex-flow: column nowrap;
    align-items: center;
    justify-content: center; }
  .slider__image {
    font-size: 2.7rem; }
  .slider__caption {
    font-weight: 700;
    margin: 0 0 1rem;
    text-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    text-transform: uppercase; }
  .slider__txt {
    color: #999;
    margin-bottom: 3rem;
    max-width: 300px; }

@keyframes check {
  50% {
    outline-color: #1769FF;
    box-shadow: 0 0 0 24px #1769FF, 0 0 0 72px rgba(23, 105, 255, 0.2); }
  100% {
    outline-color: #1769FF;
    box-shadow: 0 0 0 0 #1769FF, 0 0 0 0 rgba(23, 105, 255, 0); } }

